<template>
    <div id="container">
        <Basic active="4">
            <div id="searchbar">
                <el-form :inline="true" :model="form" class="demo-form-inline">
                    <el-form-item label="药品ID">
                        <el-input v-model="form.id" placeholder="药品ID"></el-input>
                    </el-form-item>
                    <el-form-item label="药品名称">
                        <el-input v-model="form.name" placeholder="药品名称"></el-input>
                    </el-form-item>
                    <el-form-item label="药品类型">
                        <el-input v-model="form.type" placeholder="药品类型"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="search">查询</el-button>
                    </el-form-item>
                </el-form>
            </div>
            <div id="tablebar">
                <el-table
                        :data="tableData"
                        border
                        style="width: 100%">
                    <el-table-column
                            fixed
                            prop="goodsid"
                            label="药品ID"
                            align="center"
                            sortable
                    >
                    </el-table-column>
                    <el-table-column
                            fixed
                            prop="goodsname"
                            label="药品名称"
                            align="center"
                            sortable
                    >
                    </el-table-column>
                    <el-table-column
                            prop="goodstype"
                            label="规格"
                            align="center"
                    >
                    </el-table-column>
                    <el-table-column
                            prop="price"
                            label="单价"
                            align="center"
                            sortable
                    >
                    </el-table-column>
                    <el-table-column
                            prop="goodsunit"
                            label="包装单位"
                            align="center"
                    >
                    </el-table-column>
                    <el-table-column
                            prop="type"
                            label="种类"
                            align="center"
                            sortable
                    >
                    </el-table-column>
                    <el-table-column
                            prop="medicentype"
                            label="剂型"
                            align="center"
                    >
                    </el-table-column>
                    <el-table-column
                            prop="scope"
                            label="适用范围"
                            align="center"
                    >
                    </el-table-column>
                </el-table>
                <el-row style="margin-top:30px;">
                    <el-col :span="24" style="display:flex;justify-content:center">
                        <el-pagination
                                @size-change=""
                                @current-change="handleCurrentChange"
                                :current-page.sync="currentPage"
                                :page-size="10"
                                layout="prev, pager, next, jumper"
                                :total="total">
                        </el-pagination>
                    </el-col>
                </el-row>
            </div>
        </Basic>
    </div>
</template>
<script>
    import Basic from "./Basic.vue";
    export default {
        data () {
            return {
                form: {
                    id: "",
                    name: "",
                    type: ""
                },
                tableData: [],
                total: 0,
                currentPage:1
            }
        },
        components: {
            Basic
        },
        methods: {
            handleCurrentChange: function (page) {
                this.$http.get("https://www.easy-mock.com/mock/5bed02249e6bbf680f549457/buyer/goods?p=" + page).then(res => {
                    this.tableData=res.body.data.data;
                    this.total=res.body.data.total;
                })
            },
            search: function () {
                this.$http.get(`https://www.easy-mock.com/mock/5bed02249e6bbf680f549457/buyer/goods?id=${this.form.id}&name=${this.form.name}&t=${this.form.type}`).then(res => {
                    this.$message({
                        type: 'success',
                        message: '查询成功'
                    });
                    this.tableData=res.body.data.data;
                    this.total=res.body.data.total;
                })
            }
        },
        mounted: function () {
            this.$http.get("https://www.easy-mock.com/mock/5bed02249e6bbf680f549457/buyer/goods").then(res => {
                this.tableData=res.body.data.data;
                this.total=res.body.data.total;
            })
        }
    }
</script>
<style scoped>
    #searchbar {
        width: 1200px;
        height: 100px;
        margin: 0 auto;
        margin-top: 60px;
    }

    #tablebar {
        width: 1200px;
        height: auto;
        margin: 0 auto;
    }

</style>



