<template>
    <div id="container">
        <Basic active="1">
            <div class="license">
                <h3>供应商证照</h3>
                <div class="licensebox">
                    <div class="license-item" v-for="item in clicense">
                        <img :src="item.src" alt="">
                        <p>证照名称 {{item.name}}</p>
                        <p>证照状态 <span v-if="item.status==2">已过期</span><span v-if="item.status==1">即将过期</span><span v-if="item.status==0">正常</span></p>
                    </div>
                </div>
                <div v-for="m in mlicense">
                    <h3>药品证照</h3>
                    <div class="licensebox">
                        <div class="license-item" v-for="item in m">
                            <img :src="item.src" alt="">
                            <p>证照名称 {{item.name}}</p>
                            <p>证照状态 <span v-if="item.status==2">已过期</span><span v-if="item.status==1">即将过期</span><span v-if="item.status==0">正常</span></p>
                        </div>
                    </div>
                </div>
            </div>
        </Basic>
    </div>
</template>
<script>
    import Basic from "./Basic.vue";
    export default {
        data () {
            return {
                clicense: [],
                mlicense: []
            }
        },
        methods: {
            handleCurrentChange: function (page) {
                this.$http.get("https://www.easy-mock.com/mock/5bed02249e6bbf680f549457/buyer/license?p=" + page).then(res => {
                    this.tableData=res.body.data.data;
                    this.total=res.body.data.total;
                })
            },
            search: function () {
                this.$http.get(`https://www.easy-mock.com/mock/5bed02249e6bbf680f549457/buyer/license?id=${this.form.id}&name=${this.form.name}`).then(res => {
                    this.$message({
                        type: 'success',
                        message: '查询成功'
                    });
                    this.tableData=res.body.data.data;
                    this.total=res.body.data.total;
                })
            }
        },
        components: {
            Basic
        },
        mounted: function () {
            this.$http.get("https://www.easy-mock.com/mock/5bed02249e6bbf680f549457/buyer/companylicense").then(res => {
                this.clicense=res.body.data.clicense;
                this.mlicense=res.body.data.mlicense;

            })
        }
    }
</script>
<style scoped>
    .license {
        display: block;
        width: 1200px;
        height: auto;
        margin: 0 auto;
        margin-top: 100px;
    }

    .licensebox {
        width: 1200px;
        min-height: 300px;
    }

    .licensebox .license-item {
        width: 20%;
        height: 300px;
        float: left;
        padding: 0 20px;
    }

    .license-item img {
        width: 200px;
        height: 200px;
        display: block;
    }
</style>



