<template>
    <div class="main" v-if="isLogin">
        <div id="topbar">
            <div id="container">
                <span><i class="el-icon-info"></i>  采购员 张三</span>
                <span class="logout" @click="logout">退出</span>
            </div>
        </div>
        <div id="logobar">
            <img src="../assets/logo.png" alt="">
            <el-menu
                    :default-active="active"
                    class="el-menu-demo"
                    mode="horizontal"
                    active-text-color="#44aeff"
                    :router="true"
            >
                <el-menu-item index="1" :route="{path:'/license'}">证照管理</el-menu-item>
                <el-menu-item index="2" :route="{path:'/company'}">供应商管理</el-menu-item>
                <el-menu-item index="3" :route="{path:'/agent'}">代理人管理</el-menu-item>
                <el-menu-item index="4" :route="{path:'/goods'}">货品比价</el-menu-item>
                <el-menu-item index="5"  disabled>财务结算</el-menu-item>
            </el-menu>
        </div>
        <slot></slot>
    </div>
    <div class="login" v-else>
        <div id="logo"></div>
        <div id="loginbox">
            <el-form :model="ruleForm" status-icon :rules="rules" ref="myform" label-width="100px"
                     class="demo-ruleForm">
                <el-form-item label="用户名" prop="user">
                    <el-input type="text" v-model="ruleForm.user" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="password">
                    <el-input type="password" v-model="ruleForm.password" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="submitForm()">登录</el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'Main',
        props:['active'],
        data () {
            return {
                isLogin: false,
                ruleForm: {
                    user: "",
                    password: ""
                },
                rules: {
                    user: [
                        {required: true, message: "请输入用户名", trigger: 'blur'}
                    ],
                    password: [
                        {required: true, message: "请输入密码", trigger: 'blur'}
                    ]
                },
            }
        },
        methods: {
            submitForm: function () {
                this.$refs.myform.validate((valid) => {
                    if (valid) {
                        this.$http.post("https://www.easy-mock.com/mock/5bed02249e6bbf680f549457/buyer/login", this.ruleForm).then(res => {
                            if (res.body.data.code === "200") {
                                this.$message({
                                    type: "success",
                                    message: "登录成功",
                                });
                                this.isLogin=true;
                                sessionStorage.setItem("login", "yes");
                                this.getData();
                            } else if (res.body.data.code === "10001") {
                                this.$message({
                                    type: "error",
                                    message: "用户名错误",
                                });
                            } else if (res.body.data.code === "10002") {
                                this.$message({
                                    type: "error",
                                    message: "密码错误",
                                });
                            } else {
                                this.$message({
                                    type: "error",
                                    message: "未知错误",
                                });
                            }
                        })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                })
            },
            logout: function () {
                sessionStorage.removeItem("login");
                this.$message({
                    type: "success",
                    message: "退出成功"
                });
                this.$router.push("/");
                this.isLogin=false;
            },
        },
        mounted: function () {
            if (sessionStorage.getItem('login')) {
                this.isLogin=true;
            } else {
                return;
            }
        }
    }
</script>

<style scoped>
    .login {
        position: absolute;
        background: url(../assets/bg.png) repeat-x left top/1px 100%;
        width: 100%;
        height: 100%;
    }

    #logo {
        width: 271px;
        height: 47px;
        background: url(../assets/logo.png);
        position: absolute;
        left: 140px;
        top: 50px;
    }

    #loginbox {
        width: 500px;
        height: 333px;
        border-radius: 5px;
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        margin: auto;
        background: #fff;
        padding: 40px;
    }

    #searchbar {
        width: 1200px;
        height: 100px;
        margin: 0 auto;
        margin-top: 60px;
    }

    #tablebar {
        width: 1200px;
        height: auto;
        margin: 0 auto;
    }

    .main {
        padding-bottom: 100px;
    }

    #topbar {
        width: 100%;
        height: 30px;
        background: #424242;
        color: #fff;
    }

    #logobar {
        width: 1200px;
        height:auto;
        background: #fff;
        margin: 0 auto;
        margin-top: 60px;
        overflow: hidden;
    }

    #logobar img {
        float: left;
    }

    #container {
        width: 1200px;
        height: 30px;
        line-height: 30px;
        margin: 0 auto;
    }

    .logout {
        float: right;
        cursor: pointer;
    }

    .el-menu-demo {
        width: 600px !important;
        float: left;
        margin-left: 100px;
    }
</style>



