<template>
    <div id="container">
        <div id="top">
            <div id="inner">
                <img src="../assets/logo.png" alt="">
            </div>
            <div id="title">
                <h1>欢迎入驻</h1>
                <h2>国药集团山西有限公司代理团队</h2>
            </div>
            <div id="content">
                <h3>个人信息</h3>
                <el-form :inline="true" :rules="rules" ref="myform" :model="a" style="margin-bottom:50px;">
                    <el-form-item label="代理人姓名" prop="name">
                        <el-input v-model="a.name"></el-input>
                    </el-form-item>
                    <el-form-item label="代理人联系方式" prop="phone">
                        <el-input v-model="a.phone"></el-input>
                    </el-form-item>
                </el-form>
                <h3 class="type" ref="type">药品种类
                    <el-button style="float:right;" type="primary" @click="add">添加药品</el-button>
                </h3>
                <el-row v-for="item,index in m" :key="index">
                    <el-col>
                        <h4>药品{{index + 1}}
                            <el-button style="float:right;" type="danger" @click="del(index)">移除药品</el-button>
                        </h4>
                        <el-form label-width="80px">
                            <el-form-item label="药品名称">
                                <el-input v-model="item.name"></el-input>
                            </el-form-item>
                            <el-form-item label="药品单价">
                                <el-input v-model="item.price"></el-input>
                            </el-form-item>
                            <el-form-item label="药品剂型">
                                <el-input v-model="item.type"></el-input>
                            </el-form-item>
                            <el-form-item label="药品类型">
                                <el-input v-model="item.goodstype"></el-input>
                            </el-form-item>
                            <el-form-item label="药品规格">
                                <el-input v-model="item.medicentype"></el-input>
                            </el-form-item>
                            <el-form-item label="适用范围">
                                <el-input type="textarea" v-model="item.scope"></el-input>
                            </el-form-item>
                        </el-form>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="10" :offsset="4">
                        <el-button type="primary" @click="submit">提交</el-button>
                    </el-col>
                </el-row>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data: () => ({
            a: {name: "", phone: ""},
            rules: {
                name: [{required: true, message: "请填写名称", trigger: "blur"}],
                phone: [{required: true, message: "请填写联系方式", trigger: "blur"}]
            },
            m: [{name: "", price: "", type: "", goodstype: "", medientype: "", scope: ""}]
        }),
        methods: {
            add: function () {
                this.m.push({name: "", price: "", type: "", goodstype: "", medientype: "", scope: ""});
            },
            del: function (index) {
                this.$message({
                    message: "删除成功",
                    type: "success"
                });
                this.m.splice(index, 1);
            },
            submit: function () {
                this.$refs.myform.validate((val) => {
                    if (val) {
                        let obj={
                            a: this.a,
                            m: this.m
                        };
                        this.$http.post("https://www.easy-mock.com/mock/5bed02249e6bbf680f549457/buyer/addgoods", obj).then(res => {
                            if (res.body.data.code == 200) {
                                this.$message({
                                    type: "success",
                                    message: '申请已发出'
                                })
                            }
                        })
                    } else {
                        return false;
                    }
                })

            }
        },
        mounted:function(){
            let type=this.$refs.type
            window.onscroll=function(){
                let top=document.documentElement.scrollTop;
                if(top>348){
                    type.style.cssText="position:fixed;top:0;";
                }else{
                    type.style.cssText="";
                }
            }
        }
    }
</script>

<style>
    #top {
        width: 100%;
        height: 100px;
        background: #eee;
    }

    #inner {
        width: 1200px;
        height: 100px;
        margin: 0 auto;
        display: flex;
        align-items: center;
    }

    #title {
        width: 100%;
        text-align: center;
    }

    #title h1 {
        padding: 20px;
    }

    #title h2 {
        color: #44aeff;
    }

    #content {
        width: 1200px;
        height: auto;
        margin: 0 auto;
    }

    h4 {
        border-bottom: 1px solid #ccc;
        height: 50px;
        line-height: 50px;
        margin-bottom: 15px;
    }

    h3 {
        height: 50px;
        margin-bottom: 15px;
    }

    .type {
        position: absolute;
        background:#fff;
        top:348px;
        width:1200px;
        left:50%;
        margin-left:-600px;
        z-index:9999999;
        border-bottom:1px solid #ccc;
    }
</style>



