<template>
    <div id="container">
        <Basic active="2">
            <div id="searchbar">
                <el-form :inline="true" :model="form" class="demo-form-inline">
                    <el-form-item label="供应商ID">
                        <el-input v-model="form.id" placeholder="供应商ID"></el-input>
                    </el-form-item>
                    <el-form-item label="供应商姓名">
                        <el-input v-model="form.name" placeholder="供应商姓名"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="search">查询</el-button>
                    </el-form-item>
                </el-form>
            </div>
            <div id="tablebar">
                <el-table
                        :data="tableData"
                        border
                        style="width: 100%">
                    <el-table-column
                            fixed
                            prop="companyid"
                            label="供应商id"
                            align="center"
                    >
                    </el-table-column>
                    <el-table-column
                            fixed
                            prop="companyname"
                            label="供应商名称"
                            align="center"
                    >
                    </el-table-column>
                    <el-table-column
                            prop="phone"
                            label="联系方式"
                            align="center"
                    >
                    </el-table-column>
                    <el-table-column
                            prop="goodsnum"
                            label="绑定药品数量"
                            align="center"
                    >
                    </el-table-column>
                    <el-table-column
                            fixed="right"
                            label="操作"
                            align="center"
                    >
                        <template slot-scope="scope">
                            <el-button type="success" size="medium"><a :href="'#/bindgoods?cid='+scope.row.companyid">操作</a>
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <el-row style="margin-top:30px;">
                    <el-col :span="24" style="display:flex;justify-content:center">
                        <el-pagination
                                @size-change=""
                                @current-change="handleCurrentChange"
                                :current-page.sync="currentPage"
                                :page-size="10"
                                layout="prev, pager, next, jumper"
                                :total="total">
                        </el-pagination>
                    </el-col>
                </el-row>
            </div>
        </Basic>
    </div>
</template>
<script>
    import Basic from "./Basic.vue";
    export default {
        data () {
            return {
                form: {
                    id: "",
                    name: ""
                },
                tableData: [],
                total: 0,
                currentPage:1
            }
        },
        components: {
            Basic
        },
        methods:{
            handleCurrentChange:function(page){
                this.$http.get("https://www.easy-mock.com/mock/5bed02249e6bbf680f549457/buyer/company?p=" + page).then(res => {
                    this.tableData=res.body.data.data;
                    this.total=res.body.data.total;
                })
            },
            search:function(){
                this.$http.get(`https://www.easy-mock.com/mock/5bed02249e6bbf680f549457/buyer/company?id=${this.form.id}&name=${this.form.name}`).then(res => {
                    this.$message({
                        type: 'success',
                        message: '查询成功'
                    });
                    this.tableData=res.body.data.data;
                    this.total=res.body.data.total;
                })
            }
        },
        mounted:function(){
            this.$http.get("https://www.easy-mock.com/mock/5bed02249e6bbf680f549457/buyer/company").then(res=>{
                this.tableData=res.body.data.data;
                this.total=res.body.data.total;
            })
        }
    }
</script>
<style scoped>
    #searchbar {
        width: 1200px;
        height: 100px;
        margin: 0 auto;
        margin-top: 60px;
    }

    #tablebar {
        width: 1200px;
        height: auto;
        margin: 0 auto;
    }

</style>



