<template>
    <div id="container">
        <Basic :active="active">
            <div id="searchbar">
                <el-form :inline="true" :model="form" class="demo-form-inline">
                    <el-form-item label="药品ID">
                        <el-input v-model="form.id" placeholder="药品ID"></el-input>
                    </el-form-item>
                    <el-form-item label="药品名称">
                        <el-input v-model="form.name" placeholder="药品名称"></el-input>
                    </el-form-item>
                    <el-form-item label="药品类型">
                        <el-input v-model="form.type" placeholder="药品类型"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="search">查询</el-button>
                    </el-form-item>
                </el-form>
            </div>
            <div id="tablebar">
                <el-table
                        :data="tableData"
                        border
                        style="width: 100%">
                    <el-table-column
                            fixed
                            prop="goodsid"
                            label="药品ID"
                            align="center"
                            sortable
                    >
                    </el-table-column>
                    <el-table-column
                            fixed
                            prop="goodsname"
                            label="药品名称"
                            align="center"
                            sortable
                    >
                    </el-table-column>
                    <el-table-column
                            prop="goodstype"
                            label="规格"
                            align="center"
                    >
                    </el-table-column>
                    <el-table-column
                            prop="price"
                            label="单价"
                            align="center"
                            sortable
                    >
                    </el-table-column>
                    <el-table-column
                            prop="goodsunit"
                            label="包装单位"
                            align="center"
                    >
                    </el-table-column>
                    <el-table-column
                            prop="type"
                            label="种类"
                            align="center"
                            sortable
                    >
                    </el-table-column>
                    <el-table-column
                            prop="medicentype"
                            label="剂型"
                            align="center"
                    >
                    </el-table-column>
                    <el-table-column
                            prop="scope"
                            label="适用范围"
                            align="center"
                    >
                    </el-table-column>
                    <el-table-column
                            fixed="right"
                            label="操作"
                            align="center"
                    >
                        <template slot-scope="scope">
                            <el-button v-if="scope.row.status==0" @click="binding(scope.row.goodsid)">绑定</el-button>
                            <el-button v-else @click="unbinding(scope.row.goodsid)">解除绑定</el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <el-row style="margin-top:30px;">
                    <el-col :span="24" style="display:flex;justify-content:center">
                        <el-pagination
                                @size-change=""
                                @current-change="handleCurrentChange"
                                :current-page.sync="currentPage"
                                :page-size="10"
                                layout="prev, pager, next, jumper"
                                :total="total">
                        </el-pagination>
                    </el-col>
                </el-row>
            </div>
        </Basic>
    </div>
</template>
<script>
    import Basic from "./Basic.vue";
    export default {
        data () {
            return {
                form: {
                    id: "",
                    name: "",
                    type: ""
                },
                tableData: [],
                total: 0,
                active: "",
                currentPage: 1,
                cid: 0,
                aid: 0
            }
        },
        components: {
            Basic
        },
        methods: {
            handleCurrentChange: function (page) {
                this.$http.get("https://www.easy-mock.com/mock/5bed02249e6bbf680f549457/buyer/goods?p=" + page).then(res => {
                    this.tableData=res.body.data.data;
                    this.total=res.body.data.total;
                })
            },
            search: function () {
                this.$http.get(`https://www.easy-mock.com/mock/5bed02249e6bbf680f549457/buyer/goods?id=${this.form.id}&name=${this.form.name}&t=${this.form.type}`).then(res => {
                    this.$message({
                        type: 'success',
                        message: '查询成功'
                    });
                    this.tableData=res.body.data.data;
                    this.total=res.body.data.total;
                })
            },
            binding: function (id) {
                this.$confirm('确定要绑定该药品吗?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(()=>{
                this.$http.put(`https://www.easy-mock.com/mock/5bed02249e6bbf680f549457/buyer/bindgoods?gid=${id}&cid=${this.cid}&aid=${this.aid}`).then(res => {

                    if (res.body.data.code == 200) {
                        this.$message({
                            message: "绑定成功",
                            type: "success"
                        })
                        this.getData();
                    }
                });
                }).catch(()=>{
                    this.$message({
                        type: 'info',
                        message: '已取消'
                    });
                })
            },
            unbinding: function (id) {
                this.$confirm('确定要解绑该药品吗?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(()=>{
                this.$http.put(`https://www.easy-mock.com/mock/5bed02249e6bbf680f549457/buyer/unbindgoods?gid=${id}&cid=${this.cid}&aid=${this.aid}`).then(res => {

                    if (res.body.data.code == 200) {
                        this.$message({
                            message: "解除绑定成功",
                            type: "success"
                        });
                        this.getData();
                    }

                });
                }).catch(()=>{
                    this.$message({
                        type: 'info',
                        message: '已取消'
                    });
                })
            },
            getData: function () {
                this.$http.get("https://www.easy-mock.com/mock/5bed02249e6bbf680f549457/buyer/goods").then(res => {
                    this.tableData=res.body.data.data;
                    this.total=res.body.data.total;
                });
            }
        },
        mounted: function () {
            this.getData();
            this.cid=this.$route.query.cid;
            if (this.cid) {
                this.active="2";
            }
            this.aid=this.$route.query.aid;
            if (this.aid) {
                this.active="3";
            }
        }
    }
</script>
<style scoped>
    #searchbar {
        width: 1200px;
        height: 100px;
        margin: 0 auto;
        margin-top: 60px;
    }

    #tablebar {
        width: 1200px;
        height: auto;
        margin: 0 auto;
    }

</style>



